@media (min-width: 444px) {
    .expandToRow {
        display: flex;
        flex-direction: row;
        align-items: center;
        justify-content: space-around;
        flex-wrap: nowrap
    }
}

@media (max-width: 991.98px) {
    .mobileColumn {
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
        flex-wrap: nowrap
    }
}

.sb-content-box {
    font-family: 'Libre Franklin';
    flex-grow: 1;
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column-reverse;
    align-content: stretch
}

.sb-controls-box {
    flex-grow: 4;
    position: relative
}

.sb-controls {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%)
}

.sb-wordlist-box, .sb-progress-box {
    margin: 12px
}

.sb-progress-box {
    margin-bottom: 0
}

.sb-wordlist-box {
    border-radius: 6px;
    border: 1px solid #dcdcdc;
    overflow: hidden;
    font-size: 15.5px;
    /* letter-spacing: 0.005em */
}

.sb-wordlist-box.paginated {
    overflow-y: scroll
}

.sb-wordlist-drawer {
    overflow: hidden;
    max-height: 0;
    transition: max-height 200ms ease
}

.sb-expanded .sb-wordlist-drawer {
    max-height: 50vh
}

.sb-expanded .sb-wordlist-drawer.paginated {
    max-height: calc(var(--vh, 1vh) * 68)
}

.sb-wordlist-window {
    position: relative;
    overflow: hidden;
    height: 50vh
}

.sb-wordlist-window.paginated {
    height: calc(var(--vh, 1vh) * 68)
}

.sb-controls {
    opacity: 1;
    transition: opacity 150ms 200ms ease
}

.sb-expanded .sb-controls {
    opacity: 0;
    transition: opacity 150ms ease;
    pointer-events: none
}

