.sb-progress {
    padding: 6px 12px 6px 0
}

@media (min-width: 768px) {
    .sb-progress {
        padding: 12px 12px 12px 0
    }
}

.sb-progress, .sb-progress-dots {
    cursor: pointer;
    display: flex
}

.sb-progress-rank {
    width: 93px;
    font-size: 15.5px;
    position: relative;
    top: -1.5px;
    font-weight: 700
}

.sb-progress-line {
    flex-grow: 1;
    position: relative;
    height: 9px;
    height: 1px;
    background: #dcdcdc;
    margin-top: 8px
}

.sb-progress-marker {
    width: 0;
    height: 0;
    position: absolute;
    left: 0%;
    transition: left 200ms ease
}

.sb-progress-marker .sb-progress-value {
    font-weight: 300;
    position: absolute;
    width: 30px;
    height: 30px;
    line-height: 30px;
    transform: translate(-50%, -50%);
    border-radius: 50%;
    background: #f8cd05;
    text-align: center;
    font-size: 12px;
    font-weight: 500;
    letter-spacing: -0.5px
}

.sb-progress-marker.final .sb-progress-value {
    border-radius: 0%
}

.sb-progress-dots {
    width: 100%;
    justify-content: space-between
}

.sb-progress-dot {
    display: block;
    width: 0;
    position: relative
}

.sb-progress-dot::after {
    content: '';
    display: block;
    position: absolute;
    left: -4.5px;
    top: -4.5px;
    width: 9px;
    height: 9px;
    border-radius: 50%;
    background-color: #dcdcdc;
    transition: background-color 200ms 100ms ease
}

.sb-progress-dot:last-child::after {
    border-radius: 0%
}

.sb-progress-dot.completed::after {
    background-color: #f8cd05
}

.pz-spelling-bee-wordlist .sb-progress-marker .sb-progress-value {
    background: #f7da21
}

.pz-spelling-bee-wordlist .sb-progress-marker.completed::after {
    background-color: #f7da21
}

.sb-wordlist {
    position: absolute;
    top: 0;
    bottom: 0;
    height: 100%;
    width: 100%;
    overflow-x: auto;
    overflow-y: hidden;
    -webkit-overflow-scrolling: touch
}

.sb-wordlist-pag {
    position: relative;
    margin-left: 20px;
    display: flex;
    height: 100%;
    overflow-x: auto;
    overflow-y: hidden;
    -webkit-overflow-scrolling: touch;
    z-index: 0;
    -ms-scroll-snap-type: x mandatory;
    scroll-snap-type: x mandatory;
    scrollbar-width: none;
    -ms-overflow-style: none;
    scroll-margin: 0;
    scroll-behavior: smooth;
    -ms-scroll-chaining: none;
    overscroll-behavior: contain
}

.sb-wordlist-pag::-webkit-scrollbar {
    display: none
}

.sb-wordlist-scroll-anchor {
    width: 100%;
    height: 10px;
    position: absolute;
    scroll-snap-align: start;
    scroll-snap-stop: always
}

.sb-wordlist-items-pag {
    width: 100%;
    display: flex;
    flex-flow: column wrap;
    align-content: flex-start;
    height: calc(100% - 38px);
    padding: 20px 0 24px
}

.sb-wordlist-items-pag>li {
    margin-right: 20px;
    padding: 4px 0 4px 1px;
    margin-bottom: 7px;
    border-bottom: 1px solid #dcdcdc;
    width: calc(50% - 20px)
}

.sb-wordlist-items {
    width: 95%;
    height: 100%;
    padding: 20px 0 25px 20px;
    -moz-column-count: 3;
    column-count: 3;
    -moz-column-gap: 0px;
    column-gap: 0px;
    -moz-column-fill: auto;
    column-fill: auto
}

.sb-wordlist-items>li {
    margin-right: 20px;
    display: block;
    padding: 4px 0;
    margin-bottom: 7px;
    border-bottom: 1px solid #dcdcdc
}

.sb-wordlist-heading {
    position: relative;
    overflow: hidden;
    height: 45px;
    line-height: 45px
}

.sb-placeholder-text {
    color: #ccc
}

.sb-wordlist-summary, .sb-recent-words-wrap {
    padding: 0 20px;
    position: relative;
    transition: all 270ms ease
}

.sb-wordlist-summary {
    top: -45px;
    opacity: 0;
    font-size: 1em
}

.sb-recent-words-wrap {
    top: -45px
}

.sb-expanded .sb-recent-words-wrap {
    top: 0;
    opacity: 0
}

.sb-expanded .sb-wordlist-summary {
    top: 0;
    opacity: 1
}

.sb-toggle-expand {
    display: flex;
    align-items: center;
    justify-content: center;
    position: absolute;
    top: 0;
    right: 0;
    width: 45px;
    height: 45px;
    background: #fff;
    box-shadow: 0px 0px 20px 5px #fff;
    transition: all 100ms ease;
    transform-origin: center
}

.sb-toggle-icon {
    position: relative;
    display: block;
    width: 18px;
    height: 18px;
    background: url("./assets/back.svg") center no-repeat;
    background-size: 40%;
    transform: rotate(270deg);
    transition: transform 0.25s linear
}

.sb-toggle-icon-expanded {
    transform: translateY(-1px) rotate(450deg)
}

