@-webkit-keyframes squishin {
    from {
        opacity: 0;
        max-width: 0px
    }
    to {
        max-width: 200px;
        opacity: 1
    }
}

@keyframes squishin {
    from {
        opacity: 0;
        max-width: 0px
    }
    to {
        max-width: 200px;
        opacity: 1
    }
}

.sb-recent-words {
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis
}

.sb-recent-words>li {
    max-width: 200px;
    display: inline-block;
    padding-right: 7px;
    -webkit-animation: squishin 700ms ease;
    animation: squishin 700ms ease
}

.sb-anagram {
    text-transform: capitalize;
    font-size: 1em
}

.sb-kebob {
    justify-content: center;
    display: flex;
    position: absolute;
    left: 0;
    right: 0;
    bottom: 0;
    height: 14px;
    margin: 0 0 24px
}

.sb-bob {
    display: inline-block;
    height: 8px;
    width: 8px;
    background-color: #dcdcdc;
    border-radius: 50%;
    margin: 3px 10px;
    transition: all 250ms ease
}

.sb-bob.active {
    background-color: #000
}

.sb-bob-arrow {
    border: 0;
    opacity: 0;
    visibility: hidden;
    background: url("./assets/back.svg") center no-repeat;
    background-size: contain;
    position: absolute;
    bottom: 0;
    height: 14px;
    transition: all 250ms ease;
    cursor: pointer
}

.sb-bob-arrow.sb-bob-prev {
    left: 25px
}

.sb-bob-arrow.sb-bob-next {
    right: 25px;
    transform: rotate(180deg)
}

.sb-bob-arrow.active {
    opacity: 1;
    visibility: inherit
}

.sb-bob-arrow .sb-bob-text {
    position: absolute;
    width: 1px;
    height: 1px;
    overflow: hidden;
    clip: rect(0 0 0 0)
}

@-webkit-keyframes blink {
    from, to {
        opacity: 1
    }
    50% {
        opacity: 0
    }
}

@keyframes blink {
    from, to {
        opacity: 1
    }
    50% {
        opacity: 0
    }
}

@-webkit-keyframes shake {
    10%, 90% {
        transform: translate3d(-1px, 0, 0)
    }
    20%, 80% {
        transform: translate3d(2px, 0, 0)
    }
    30%, 50%, 70% {
        transform: translate3d(-4px, 0, 0)
    }
    40%, 60% {
        transform: translate3d(4px, 0, 0)
    }
}

@keyframes shake {
    10%, 90% {
        transform: translate3d(-1px, 0, 0)
    }
    20%, 80% {
        transform: translate3d(2px, 0, 0)
    }
    30%, 50%, 70% {
        transform: translate3d(-4px, 0, 0)
    }
    40%, 60% {
        transform: translate3d(4px, 0, 0)
    }
}

.sb-hive-input {
    text-transform: uppercase;
    font-size: 32px;
    font-weight: 700;
    letter-spacing: .5px;
    text-rendering: optimizeLegibility;
    line-height: 40px;
    height: 40px;
    padding-top: 2px;
    padding-right: 14px;
    text-align: center
}

.sb-hive-input-content {
    display: inline-block;
    position: relative;
    transform: translateY(0%)
}

.sb-hive-input-content.non-empty {
    padding-left: 14px
}

.sb-hive-input-content.has-error {
    -webkit-animation: shake 0.9s cubic-bezier(0.36, 0.07, 0.19, 0.97) both;
    animation: shake 0.9s cubic-bezier(0.36, 0.07, 0.19, 0.97) both
}

.sb-hive-input-content::after {
    content: '';
    display: block;
    position: absolute;
    right: -4px;
    top: 0;
    width: 2px;
    height: 40px;
    background: #f8cd05;
    -webkit-animation: 1000ms blink step-end infinite;
    animation: 1000ms blink step-end infinite
}

.sb-game-locked .sb-hive-input-content::after {
    display: none
}

.sb-input-bright {
    color: #f8cd05
}

.sb-input-invalid {
    color: #dcdcdc
}

