.sb-hive {
    width: 90%;
    margin: 25px auto;
    -webkit-tap-highlight-color: transparent;
    -webkit-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none
}

.hive {
    position: relative;
    width: 100%;
    padding-bottom: 103.92305%
}

.hive .cell-letter {
    font-weight: 700;
    font-size: 30px;
    text-anchor: middle;
    text-transform: uppercase;
    pointer-events: none
}

.hive.shuffling .outer>.cell-letter {
    opacity: 0
}

.hive.fade-out .outer>.cell-letter {
    opacity: 0;
    transition: opacity .3s
}

.hive.fade-in .outer>.cell-letter {
    opacity: 1;
    transition: opacity .3s
}

.hive-cell {
    position: absolute;
    top: calc(100% / 3);
    left: 30%;
    width: 40%;
    height: calc(100% / 3)
}

.hive-cell .cell-fill {
    cursor: pointer;
    fill: #e6e6e6;
    transition: all 100ms
}

.hive-cell .cell-fill.push-active {
    fill: #d9d9d9
}

.hive-cell:first-child .cell-fill {
    cursor: pointer;
    fill: #f8cd05;
    transition: all 100ms
}

.hive-cell:first-child .cell-fill.push-active {
    fill: #dfb804
}

.hive-cell>.cell-fill.push-active {
    transform: scale3d(0.86, 0.86, 1);
    transform-origin: center center
}

.hive-cell:nth-child(1) {
    transform: translate(0, 0)
}

.hive-cell:nth-child(2) {
    transform: translate(-75%, -50%)
}

.hive-cell:nth-child(3) {
    transform: translate(0, -100%)
}

.hive-cell:nth-child(4) {
    transform: translate(75%, -50%)
}

.hive-cell:nth-child(5) {
    transform: translate(75%, 50%)
}

.hive-cell:nth-child(6) {
    transform: translate(0, 100%)
}

.hive-cell:nth-child(7) {
    transform: translate(-75%, 50%)
}

.hive-actions {
    text-align: center;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-between;
    flex-wrap: nowrap
}

.hive-action {
