/* copied from foundation.css */

.pz-moment__frame:not(.on-stage) {
    display: none;
}


.pz-moments {
    transition:minHeight 200ms
   }
   .pz-moment {
    width:100%;
    color:#000;
    margin:0 auto;
    min-height:calc(100vh - 55px);
    text-align:center;
    padding:0 15px
   }
   .pz-moment__container {
    display:flex;
    flex-direction:column;
    align-items:center;
    justify-content:space-around;
    flex-wrap:nowrap;
    position:relative;
    padding:0 15px;
    height:100%
   }
   .pz-moment__container.alternate {
    padding:30px 15px;
    justify-content:flex-start
   }
   .sequence-animation>* {
    opacity:0;
    transform:translateY(20px)
   }
   .pz-moment__content {
    max-width:100%
   }
   @media (min-width: 768px) {
    .pz-moment__content {
     max-width:50%
    }
   }
   .pz-moment__frame {
    width:100%;
    left:0;
    right:0;
    min-height:calc(100vh - 55px);
    font:-apple-system-body
   }
   .pz-moment__frame:not(.on-stage) {
    display:none
   }
   .pz-moment__frame.fly-in {
    -webkit-animation:flyIn 600ms;
    animation:flyIn 600ms;
    -webkit-animation-timing-function:ease;
    animation-timing-function:ease
   }
   .pz-moment__frame.fly-out {
    -webkit-animation:flyOut 600ms;
    animation:flyOut 600ms;
    -webkit-animation-timing-function:ease;
    animation-timing-function:ease;
    -webkit-animation-timing-function:cubic-bezier(1, -0.165, 0.11, 0.925);
    animation-timing-function:cubic-bezier(1, -0.165, 0.11, 0.925);
    -webkit-animation-timing-function:cubic-bezier(1, -0.2, 0, 1.005);
    animation-timing-function:cubic-bezier(1, -0.2, 0, 1.005);
    opacity:0
   }
   .slide-up {
    opacity:1;
    transform:translateY(0);
    transition:all 600ms cubic-bezier(0.1, 0.2, 0.3, 1)
   }
   .pz-moment__loading {
    -webkit-animation:fadeIn 1200ms;
    animation:fadeIn 1200ms;
    -webkit-animation-timing-function:ease-out;
    animation-timing-function:ease-out;
    padding:30px;
    text-align:center;
    min-height:calc(100vh - 55px)
   }
   @media (min-width: 768px) {
    .pz-moment__loading {
     padding-left:calc(25% + 15px);
     padding-right:calc(25% + 15px)
    }
   }
   .pz-loader {
    width:150px;
    height:8px;
    border-radius:104px;
    margin:0 auto;
    position:relative
   }
   .pz-loader__fill {
    position:absolute;
    top:0;
    left:0;
    bottom:0;
    right:98%;
    border-radius:104px;
    transition:right 200ms ease
   }
   @media (min-width: 768px) {
    .pz-moment__button-wrapper {
     display:flex;
     flex-direction:row-reverse;
     justify-content:center
    }
   }
   .pz-moment__button {
    position:relative;
    border:none;
    height:3em;
    border-radius:1.5em;
    align-content:center;
    font:600 1em "Libre Franklin";
    letter-spacing:0.05em;
    margin:0 10px 8px;
    background:#000;
    color:#fff;
    min-width:9.375em
   }
   .pz-moment__button:last-of-type {
    margin-bottom:24px
   }
   @media (min-width: 768px) {
    .pz-moment__button:last-of-type {
