    border:1px solid;
    letter-spacing:0.01em
   }
   .pz-moment__button.wide {
    min-width:11.25em
   }
   @media (max-width: 767.98px) {
    .pz-moment__button {
     display:block;
     margin-left:auto;
     margin-right:auto;
     text-align:center
    }
   }
   .pz-moment__button:focus {
    outline:none
   }
   .pz-moment__button:focus::after {
    content:'';
    display:block;
    position:absolute;
    top:-4px;
    left:-4px;
    right:-4px;
    bottom:-4px;
    border:2px dotted lightblue;
    border-radius:1.875em
   }
   .pz-moment__button:hover {
    cursor:pointer
   }
