    background-image:url("./assets/padlock.svg")
   }
   .pz-moment__close {
    position:fixed;
    top:45px;
    right:0;
    background:none;
    border:0;
    padding:28px 24px;
    text-indent:-9999px;
    width:calc(12px + 24px * 2);
    height:calc(12px + 28px * 2);
    cursor:pointer
   }
   .pz-moment__close::before,
   .pz-moment__close::after {
    content:'';
    position:absolute;
    width:calc(4 / 3 * 12px);
    height:2px;
    top:calc(28px + 12px / 2);
    left:calc(24px - 12px / 6);
    background:#000;
    transform:rotate(-45deg)
   }
   .pz-moment__close::after {
    transform:rotate(-135deg)
   }
   @media (min-width: 768px) {
    .pz-moment__close {
     width:calc(24px + 24px * 2);
     height:calc(24px + 28px * 2)
    }
    .pz-moment__close::before,
    .pz-moment__close::after {
     width:calc(4 / 3 * 24px);
     top:calc(28px + 24px / 2);
     left:calc(24px - 24px / 6)
    }
   }
